.App {
  text-align: center;
  min-height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

// Fade animations for CSSTransition component
.fade-enter,
.fade-appear {
  opacity: 0;
  //transform: scale(0.9);
}

.fade-enter-active,
.fade-appear-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 1000ms, transform 300ms;
  //animation-name: fadeIn, zoomIn;
  //animation-duration: 1s, 1s;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  //transform: scale(0.9);
  transition: opacity 1000ms, transform 300ms;
}
